
import { defineComponent } from "@vue/runtime-core";

export default defineComponent({
    name: 'FormInput',

    props: {
        name: {
            type: String,
            required: true
        },
        type: {
            type: String,
            default: 'text'
        },
        modelValue: {
            type: String,
            required: true
        },
        error: {
            type: String,
        }
    },

    setup(props, ctx) {
        const handleInput = (e: Event) => {
            ctx.emit('update:modelValue', (e.target as HTMLInputElement).value);
        }

        return {
            handleInput,
        }
    }
})

